import React, { useEffect, useState, useRef } from "react";
import { Route, Link, withRouter } from "react-router-dom";
import { AiOutlineCheck } from "react-icons/ai";
import Subtitle from "../../../components/Subtitle";
import axios from "axios";

const WriteVolunteer = ({ history }) => {
	const [info, setInfo] = useState({
		name: "",
		birth: "",
		phoneNumber: "",
		vms: "",
		activity: "",
		hopeContent: "",
		hopeTime: "",
		agree: false,
	});

	const nameRef = useRef(null);
	const birthRef = useRef(null);
	const phoneNumberRef = useRef(null);
	const vmsRef = useRef(null);
	const activityRef = useRef(null);
	const hopeContentRef = useRef(null);
	const hopeTimeRef = useRef(null);

	const submitInfo = () => {
		if (info.name === "") {
			alert(" !");
			nameRef.current.focus();
		} else if (info.birth === "") {
			alert(" !");
			birthRef.current.focus();
		} else if (info.birth.length !== 6) {
			alert("  6 .");
			birthRef.current.focus();
		} else if (info.phoneNumber === "") {
			alert(" !");
			phoneNumberRef.current.focus();
		} else if (info.phoneNumber.includes("-")) {
			alert("(-)   .");
			phoneNumberRef.current.focus();
		} else if (info.vms === "") {
			alert("VMS ID/ !");
			vmsRef.current.focus();
		} else if (info.activity === "") {
			alert("   .");
			activityRef.current.focus();
		} else if (info.hopeContent === "") {
			alert("  .");
			hopeContentRef.current.focus();
		} else if (info.hopeTime === "") {
			alert("  .");
			hopeTimeRef.current.focus();
		} else if (!info.agree) {
			alert("     ");
		} else {
			axios
				.post(
					"/api/volunteer/add/unread",
					{
						key: process.env.REACT_APP_API_KEY,
						name: info.name,
						birth: info.birth,
						phone: info.phoneNumber,
						vms: info.vms,
						activity: info.activity,
						hopeContent: info.hopeContent,
						hopeTime: info.hopeTime,
					},
					{
						headers: {
							"Content-type": "application/json",
							Accept: "application/json",
						},
					}
				)
				.then((response) => {
					window.scrollTo(0, 0);
					alert("!");
					history.push("/participation/volunteer/0");
					document.getElementById("scrollRef").scrollTo(0, 0);
				})
				.catch((response) => {
					console.log("Error!");
				});
		}
	};
	const changeInfo = (e, type) => {
		const cp = { ...info };
		cp[type] = e.target.value;
		setInfo(cp);
	};

	const toggleAgree = (e, type) => {
		const cp = { ...info };
		cp[type] = !cp[type];
		setInfo(cp);
	};

	return (
		<div class="w-full h-auto">
			<div class="flex flex-row justify-between items-center mb-4 md:mb-8">
				<div>
					<Subtitle text={" "} />
				</div>
			</div>
			<form class="border-t-2 border-b-2 border-purple-700 py-6 mb-4">
				<div class="mb-4">
					<h1 class="text-md mb-4 font-bold"> </h1>
				</div>
				<div class="flex flex-row justify-start items-center mb-2">
					<input
						ref={nameRef}
						type="text"
						class="w-1/2 p-4 transition delay-50 duration-300 border-2 border-gray-300 outline-none mr-4 focus:border-purple-700"
						onChange={(e) => changeInfo(e, "name")}
						placeholder="()"
					/>
					<input
						ref={birthRef}
						type="text"
						class="w-1/2 p-4 transition delay-50 duration-300 border-2 border-gray-300 outline-none focus:border-purple-700"
						onChange={(e) => changeInfo(e, "birth")}
						placeholder="  6"
					/>
				</div>
				<div class="flex flex-row justify-start items-center mb-2">
					<input
						ref={phoneNumberRef}
						type="text"
						class="w-1/2 p-4 transition delay-50 duration-300 border-2 border-gray-300 outline-none mr-4 focus:border-purple-700"
						onChange={(e) => changeInfo(e, "phoneNumber")}
						placeholder=" (  )"
					/>
					<input
						ref={vmsRef}
						type="text"
						class="w-1/2 p-4 transition delay-50 duration-300 border-2 border-gray-300 outline-none focus:border-purple-700"
						onChange={(e) => changeInfo(e, "vms")}
						placeholder="VMS ID/"
					/>
				</div>
				<div class="flex flex-row justify-start items-center mb-2">
					<input
						ref={activityRef}
						type="text"
						class="w-full p-4 transition delay-50 duration-300 border-2 border-gray-300 outline-none focus:border-purple-700"
						onChange={(e) => changeInfo(e, "activity")}
						placeholder=""
					/>
				</div>
				<div class="flex flex-row justify-start items-center mb-2">
					<input
						ref={hopeContentRef}
						type="text"
						class="w-3/4 p-4 transition delay-50 duration-300 border-2 border-gray-300 outline-none mr-4 focus:border-purple-700"
						onChange={(e) => changeInfo(e, "hopeContent")}
						placeholder=""
					/>
					<input
						ref={hopeTimeRef}
						type="text"
						class="w-1/4 p-4 transition delay-50 duration-300 border-2 border-gray-300 outline-none focus:border-purple-700"
						onChange={(e) => changeInfo(e, "hopeTime")}
						placeholder=" "
					/>
				</div>

				<div class="mt-8 mb-4">
					<h1 class="text-md mb-4 font-bold"></h1>
				</div>
				<div class="text-xs md:text-base leading-5 md:leading-7 w-full h-full border border-black p-4 mb-8">
					<div>
						     ,  
						        
						.      
						      .
					</div>
					<div class="w-full h-full p-4 border border-black my-4">
						  .   
						       
						    . ,   
						          .
					</div>
					<div class="leading-5 md:leading-7">
						<h1>
							<span> </span>:  ,   
							{" "}
						</h1>
						<h1>
							<span class="invisible"> </span>- : ,
							(),  , ,  
						</h1>
						<h1>
							<span> </span>   
						</h1>
						<h1>
							<span class="invisible"> </span>-  {" "}
						</h1>
						<h1>
							<span> </span>   : 
						</h1>
						<h1>
							<span class="invisible"> </span>-   
							       
							    .
						</h1>
					</div>
				</div>
				<div
					onClick={(e) => toggleAgree(e, "agree")}
					class="flex flex-row items-center mb-4"
				>
					<div
						class={
							"cursor-pointer w-5 h-5 border-2 rounded-sm border-purple-700 flex justify-center items-center " +
							(info.agree ? "bg-purple-700" : "bg-white")
						}
					>
						{info.agree && <AiOutlineCheck size={20} class="text-white" />}
					</div>
					<div class="flex-1 cursor-pointer w-auto mx-4 text-sm font-bold">
						       
						.
					</div>
				</div>
			</form>
			<div class="flex flex-col md:flex-row justify-between items-center">
				<Link
					class="w-full md:w-auto mb-4 md:mb-4 transition delay-50 duration-300 justify-center cursor-pointer px-16 py-2 border border-purple-700 text-purple-700 flex flex-row items-center hover:bg-purple-500 hover:text-white hover:font-bold"
					to="/participation/volunteer/0"
					onClick={() => document.getElementById("scrollRef").scrollTo(0, 0)}
				>
					 
				</Link>
				<button
					onClick={submitInfo}
					class="w-full md:w-auto cursor-pointer transition delay-50 duration-300 justify-center px-16 py-2 border border-purple-700 text-purple-700 flex flex-row items-center hover:bg-purple-500 hover:text-white hover:font-bold "
				>
					
				</button>
			</div>
		</div>
	);
};

export default withRouter(WriteVolunteer);
